<template>
    <div>
		<el-table :data="tableDate">
			<el-table-column label="名称" type="selection" align="center"></el-table-column>
			<el-table-column prop="number" label="学号" align="center"></el-table-column>
			<el-table-column prop="name" label="学生姓名" align="center"></el-table-column>
			<el-table-column prop="id" label="家长ID" align="center"></el-table-column>
			<el-table-column prop="_name" label="家长姓名" align="center"></el-table-column>
			<el-table-column prop="phone" label="家长手机号" align="center"></el-table-column>
			<el-table-column prop="remark" label="状态" align="center"></el-table-column>
			<el-table-column prop="remark" label="更新时间" align="center"></el-table-column>
			<el-table-column label="操作" align="center">
				<template slot-scope="scope">
					<el-button size="mini" @click="handleMore(scope.$index, scope.row)">查看更多</el-button>
				</template>
			</el-table-column>
		</el-table>
		
		<div class="block">
			<el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="prev, pager, next, jumper"
			 :total="totalPage">
			</el-pagination>
		</div>
		
		<el-dialog title="学生家庭成员列表" :visible.sync="dialogFamily" width='80%' align="center">
			<table border="1" width="1200px" class="table" :model="studentList">
				<tr class="trHeight">
					<td rowspan='2'>学生信息</td>
					<td class="bgcolor">学号</td>
					<td class="bgcolor">学生姓名</td>
					<td class="bgcolor">性别</td>
					<td class="bgcolor">年龄</td>
					<td class="bgcolor">年级</td>
					<td class="bgcolor">班级</td>
				</tr>
				<tr>
					<td>{{studentList.number}}</td>
					<td>{{studentList.name}}</td>
					<td>{{studentList.sex === 1 ? '男' : '女'}}</td>
					<td>{{studentList.age}}</td>
					<td>{{studentList.class_id}}</td>
					<td>{{studentList.class_id}}</td>
				</tr>
			</table>
			<table border="1" width="1200px" class="table">
				<tr class="trHeight">
					<td rowspan='2'>默认家长</td>
					<td class="bgcolor">家长ID</td>
					<td class="bgcolor">家长名称</td>
					<td class="bgcolor">性别</td>
					<td class="bgcolor">电话</td>
					<td class="bgcolor">身份证</td>
					<td class="bgcolor">地址</td>
				</tr>
				<tr>
					<td></td>
					<td></td>
					<td></td>
					<td></td>
					<td></td>
					<td></td>
				</tr>
			</table>
			<div v-for="(item,index) in tableList" :key="index">
				<table border="1" width="1200px" class="table">
					<tr class="trHeight">
						<td rowspan='2'>家长信息</td>
						<td class="bgcolor">家长ID</td>
						<td class="bgcolor">家长名称</td>
						<td class="bgcolor">性别</td>
						<td class="bgcolor">电话</td>
						<td class="bgcolor">身份证</td>
						<td class="bgcolor">地址</td>
					</tr>
					<tr>
						<td></td>
						<td></td>
						<td></td>
						<td></td>
						<td></td>
						<td></td>
					</tr>
				</table>
			</div>
		</el-dialog>
	</div>
</template>

<script>
	import API from '@/api/student.js'
	export default {
		name: 'studentfamily',
		data() {
			return {
				tableDate: [{}],
				dialogFamily: false,
				studentList: {},
				tableList: [{}],
				currentPage: 1,
				totalPage: 0
			}
		},
		mounted() {
			this.getStudent()
		},
		methods: {
			// 获取学生列表
			getStudent() {
				var self = this;
				API.students(self.currentPage).then(res => {
					self.tableDate = res.data;
					self.totalPage = res.total;
				})
			},
			handleMore(index, row) {
				var self = this;
				self.dialogFamily = true;
				self.studentList = row;
			},
			
			// 返回得数据有多个家长信息，推入一个表格
			getMoreInfo() {
				this.tableList.push({})
			},
			
			
			// 分页
			handleCurrentChange(val) {
				var self = this;
				this.getStudent()
			},
		}
	}
</script>

<style lang="scss" scoped>
	.table {
		height: 100px;
		text-align: center;
		box-shadow: 0 2px 4px 0;
		margin-top: 20px;
	}

	.table .bgcolor {
		background-color: #EBEEF5;
	}

	.table .trHeight {
		height: 40px;
	}

	.table td {
		width: 40px;
	}
</style>

